.article {
  line-height: 1.2;

  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__publication-date {
    align-self: flex-end;
  }
  &__social-btn {
    flex-basis: 80%;
  }
  //text
  h1 {
    font-size: $noto__sans-bold;
  }
  p {
    margin: rem(15px) 0 0 0;
  }
}

//links
.article__internal-link {
  color: $yellow;
  font-family: $roboto__bold;
  padding: 0 rem(10px);
  white-space: nowrap;
  &:before {
    content: '';
    display: inline-block;
    content: '';
    width: 15px;
    height: 15px;
    padding: 0 rem(5px) 0 0;
    background-size: 15px;
    background: url("../img/link.svg") no-repeat;
  }
  &:hover {
    text-decoration: underline;
  }
}

.article__out-link {
  color: $yellow;
  font-family: $roboto__bold;
  padding: 0 rem(10px);
  white-space: nowrap;
  &:before {
    display: inline-block;
    content: '';
    width: 15px;
    height: 15px;
    padding: 0 rem(5px) 0 0;
    background-size: 15px;
    background: url(../img/out-link.svg) no-repeat;
  }
  &:hover {
    text-decoration: underline;
  }
}

.images__slider {
  position: relative;
  display: flex;
  flex-direction: column;

  &-browsing {
    width: 100%;
    overflow: hidden;
  }
  &-review {
    display: block;
    width: 100%;
  }
  .pagination__img-list {
    display: flex;
    width: 100%;
    overflow: hidden;
  }
  &-prev {
    filter: grayscale(70%);
    display: block;
    width: 100%;
  }
  &_active-item {
    border: 5px solid $yellow;
    filter: grayscale(0);
  }
  &-controls {
    position: absolute;
    top: 40%;
    width: 100%;
    opacity: .4;
    &:hover {
      opacity: 1;
    }
  }
}